'use strict';

// MODULES //

import isArrayLikeObject from '@stdlib/assert-is-array-like-object' ;
import isndarrayLike from '@stdlib/assert-is-ndarray-like' ;
import isFunction from '@stdlib/assert-is-function' ;
import ndarraylike2object from '@stdlib/ndarray-base-ndarraylike2object' ;
import arraylike2object from '@stdlib/array-base-arraylike2object' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;
import ndarrayFcn from './ndarray.js' ;
import arrayFcn from './array.js' ;


// MAIN //

/**
* Performs a map-reduce operation against each element in an array while iterating from right to left and returns the accumulated result.
*
* ## Notes
*
* -   The mapping function is provided the following arguments:
*
*     -   **value**: array element.
*     -   **index**: element index.
*     -   **arr**: input array.
*
* -   The reducing function is provided the following arguments:
*
*     -   **accumulator**: accumulated value.
*     -   **value**: result of applying the mapping function against the current array element.
*     -   **index**: element index.
*     -   **arr**: input array.
*
* @param {(ArrayLikeObject|ndarray)} arr - input array
* @param {*} initial - initial value
* @param {Function} mapper - mapping function
* @param {Function} reducer - reducing function
* @param {*} [thisArg] - reduction function execution context
* @throws {TypeError} first argument must be an array-like object or an ndarray
* @throws {TypeError} third argument must be a function
* @throws {TypeError} fourth argument must be a function
* @returns {*} accumulated result
*
* @example
* function square( value ) {
*     return value * value;
* }
*
* function sum( acc, value ) {
*     return acc + value;
* }
*
* var arr = [ 1, 2, 3, 4 ];
*
* var out = mapReduceRight( arr, 0, square, sum );
* // returns 30
*
* @example
* import naryFunction from '@stdlib/utils-nary-function' ;
* import add from '@stdlib/math-base-ops-add' ;
* import abs from '@stdlib/math-base-special-abs' ;
* import array from '@stdlib/ndarray-array' ;
*
* var opts = {
*     'dtype': 'generic'
* };
* var arr = array( [ [ -1, -2, -3 ], [ -4, -5, -6 ] ], opts );
*
* var out = mapReduceRight( arr, 0, naryFunction( abs, 1 ), naryFunction( add, 2 ) );
* // returns 21
*/
function mapReduceRight( arr, initial, mapper, reducer, thisArg ) {
	if ( !isFunction( mapper ) ) {
		throw new TypeError( format( '0kW3Z', mapper ) );
	}
	if ( !isFunction( reducer ) ) {
		throw new TypeError( format( '0kW3E', reducer ) );
	}
	if ( isndarrayLike( arr ) ) { // note: assertion order matters here, as an ndarray-like object is also array-like
		return ndarrayFcn( ndarraylike2object( arr ), initial, mapper, reducer, thisArg ); // eslint-disable-line max-len
	}
	if ( isArrayLikeObject( arr ) ) {
		return arrayFcn( arraylike2object( arr ), initial, mapper, reducer, thisArg ); // eslint-disable-line max-len
	}
	throw new TypeError( format( '0kWC4', arr ) );
}


// EXPORTS //

export default mapReduceRight;
